import { useEffect } from 'react';
import { InnerBlocks } from '@wordpress/block-editor';

import { tabController } from '../../../../Components/utils/functions';

import Settings from './Settings/Settings';
import Style from '../Common/Style';

const Edit = props => {
	const { className, attributes, setAttributes, clientId, isSelected } = props;
	const { layout, columns } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	useEffect(() => tabController(), [isSelected]);

	return <>
		<Settings attributes={props.attributes} setAttributes={setAttributes} clientId={clientId} />

		<div className={className} id={`ssbServices-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} isBackend={true} />

			<div className={`ssbServices ${layout} columns-${columns.desktop} columns-tablet-${columns.tablet} columns-mobile-${columns.mobile}`}>
				<InnerBlocks allowedBlocks={['services-section/service']} template={[['services-section/service'], ['services-section/service'], ['services-section/service']]} />
			</div>
		</div>
	</>;
};
export default Edit;