import { useEffect } from 'react';
import { RichText } from '@wordpress/block-editor';
import { __ } from '@wordpress/i18n';

import Settings from './Settings/Settings';
import Style from '../Common/Style';
import Service from '../Common/Service';

const Edit = props => {
	const { className, attributes, setAttributes, clientId } = props;
	const { isTitle, title, isDesc, desc } = attributes;

	useEffect(() => { clientId && setAttributes({ cId: clientId.substring(0, 10) }); }, [clientId]); // Set & Update clientId to cId

	// Get Patent Attributes
	// const parentBlock = select('core/block-editor').getBlockParents(clientId);
	// const parentAttributes = select('core/block-editor').getBlockAttributes(parentBlock);

	return <>
		<Settings attributes={props.attributes} setAttributes={setAttributes} />

		<div className={className} id={`ssbService-${clientId}`}>
			<Style attributes={attributes} clientId={clientId} />

			<Service attributes={attributes}>
				{isTitle && <>
					{<RichText className='title' tagName='h3' value={title} onChange={val => setAttributes({ title: val })} placeholder={__('Service Title', 'services-section')} inlineToolbar />}
				</>}

				{isDesc && <RichText className='description' tagName='p' value={desc} onChange={val => setAttributes({ desc: val })} placeholder={__('Service Description', 'services-section')} inlineToolbar />}
			</Service>
		</div>
	</>;
};
export default Edit;